import React, { useState } from "react"
import "./template.css"

const Page = ({ pageContext: { font } }) => {
  const [text, setText] = useState("")
  const [fontSize, setFontSize] = useState("24px")
  const [fontStyle, setFontStyle] = useState("normal")
  const fontWeights = [100, 200, 300, 400, 500, 600, 700, 800, 900]

  const textHandler = event => {
    setText(event.target.value)
  }

  const useText =
    text ||
    'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz01234567890;/.,?":'
  const useFont = font || "Open Sans"

  return (
    <div className="font-preview " style={{ fontFamily: useFont }}>
      <div className="container">
        <a href="/fonts-galore">
          <button id="back-button">Back</button>
        </a>
        <div className="font-title">{useFont}</div>
        <div className="preview-inputs">
          <input
            type="text"
            onChange={textHandler}
            placeholder="Type to preview!"
          />
          <div>
            <select
              value={fontSize}
              onChange={event => setFontSize(event.target.value)}
            >
              <option value={"12px"}>12</option>
              <option value={"16px"}>16</option>
              <option value={"24px"}>24</option>
              <option value={"32px"}>32</option>
              <option value={"48px"}>48</option>
              <option value={"64px"}>64</option>
              <option value={"96px"}>96</option>
            </select>
            <select
              value={fontStyle}
              onChange={event => setFontStyle(event.target.value)}
            >
              <option value="normal">Normal</option>
              <option value="italic">Italic</option>
            </select>
          </div>
        </div>
        <div className="font-weight-container">
          {fontWeights.map(fontWeight => {
            return (
              <div
                key={fontWeight}
                className="font-weight-row"
                style={{
                  fontWeight: fontWeight,
                  fontSize: fontSize,
                  fontStyle: fontStyle,
                }}
              >
                <div>{fontWeight}</div>
                <div>{useText}</div>
              </div>
            )
          })}
        </div>
        <div className="add-font">
          <code>
            npm install --save typeface-
            {useFont.toLowerCase().replace(" ", "-")}
          </code>
          <code>
            require("typeface-{useFont.toLowerCase().replace(" ", "-")}")
          </code>
        </div>
      </div>
    </div>
  )
}

export default Page
